import React, { useState } from "react";
import Avatar from "@material-ui/core/Avatar";
import Button from "@material-ui/core/Button";
import CssBaseline from "@material-ui/core/CssBaseline";
import TextField from "@material-ui/core/TextField";
import FormControlLabel from "@material-ui/core/FormControlLabel";
import Checkbox from "@material-ui/core/Checkbox";
import Link from "@material-ui/core/Link";
import Grid from "@material-ui/core/Grid";
import Box from "@material-ui/core/Box";
import LockOutlinedIcon from "@material-ui/icons/LockOutlined";
import Typography from "@material-ui/core/Typography";
import { makeStyles } from "@material-ui/core/styles";
import Container from "@material-ui/core/Container";

import { login } from "../UserFunctions/UserFunctions";

function Copyright() {
  return (
    <Typography variant="body2" color="textSecondary" align="center">
      {"Copyright © "}
      <Link color="inherit" href="https://material-ui.com/">
        arijitchowdhury.me
      </Link>{" "}
      {new Date().getFullYear()}
      {"."}
    </Typography>
  );
}

const useStyles = makeStyles((theme) => ({
  paper: {
    marginTop: theme.spacing(8),
    display: "flex",
    flexDirection: "column",
    alignItems: "center",
  },
  avatar: {
    margin: theme.spacing(1),
    backgroundColor: theme.palette.secondary.main,
  },
  form: {
    width: "100%", // Fix IE 11 issue.
    marginTop: theme.spacing(1),
  },
  submit: {
    margin: theme.spacing(3, 0, 2),
  },
}));

const Login = ({ history }) => {
  const classes = useStyles();
  const [emailPhone, setEmailPhone] = useState("");
  const [otp, setOtp] = useState("");

  const [errorEmailPhone, setErrorEmailPhone] = useState("");
  const [errorOtp, setErrorOtp] = useState("");

  const handleEmailPhoneValidation = (emailPhoneVal) => {
    const email_regex = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
    let error = "Invalid input";
    if (
      email_regex.test(emailPhoneVal) ||
      (emailPhoneVal.length === 10 &&
        Number.isInteger(parseInt(emailPhoneVal)) &&
        emailPhoneVal.length === parseInt(emailPhoneVal).toString().length)
    )
      error = "";
    setErrorEmailPhone(error);
  };

  const handleOtpValidation = (otpVal) => {
    let error = "";
    if (otpVal.length !== 6) error = "Invalid OTP";
    setErrorOtp(error);
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    console.log(emailPhone, otp);
    try {
      const body = {
        email_phone: emailPhone,
        otp,
      };
      await login(body);
      history.push("/");
    } catch (err) {
      console.log(err);
      alert("Invalid Email/Phone No/ OTP");
    }
  };

  return (
    <Container component="main" maxWidth="xs">
      <CssBaseline />
      <div className={classes.paper}>
        <Avatar className={classes.avatar}>
          <LockOutlinedIcon />
        </Avatar>
        <Typography component="h1" variant="h5">
          Sign in
        </Typography>
        <form className={classes.form} noValidate>
          <TextField
            variant="outlined"
            margin="normal"
            required
            fullWidth
            id="emailPhoneno"
            label="Email / Phone No"
            name="emailPhoneno"
            autoComplete="emailPhoneno"
            value={emailPhone}
            onChange={(e) => {
              setEmailPhone(e.target.value);
              handleEmailPhoneValidation(e.target.value);
            }}
            error={errorEmailPhone.length > 0}
            helperText={errorEmailPhone}
            onBlur={() => handleEmailPhoneValidation(emailPhone)}
          />
          <TextField
            variant="outlined"
            margin="normal"
            required
            fullWidth
            id="otp"
            label="OTP"
            name="otp"
            autoComplete="otp"
            type="number"
            value={otp}
            onChange={(e) => {
              setOtp(e.target.value);
              handleOtpValidation(e.target.value);
            }}
            error={errorOtp.length > 0}
            helperText={errorOtp}
            onBlur={() => handleOtpValidation(otp)}
          />
          <FormControlLabel
            control={<Checkbox value="remember" color="primary" />}
            label="Remember me"
          />
          <Button
            type="submit"
            fullWidth
            variant="contained"
            color="primary"
            className={classes.submit}
            onClick={(e) => handleSubmit(e)}
          >
            Sign In
          </Button>
          <Grid container>
            <Grid item xs>
              <Link href="#" variant="body2">
                Forgot password?
              </Link>
            </Grid>
            <Grid item>
              <Link href="/register" variant="body2">
                {"Don't have an account? Sign Up"}
              </Link>
            </Grid>
          </Grid>
        </form>
      </div>
      <Box mt={8}>
        <Copyright />
      </Box>
    </Container>
  );
};

export default Login;
